"use client"

import type React from "react"

import { useState, useRef } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Upload, AlertCircle, CheckCircle, XCircle } from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"

export default function Home() {
  const [file, setFile] = useState<File | null>(null)
  const [videoSrc, setVideoSrc] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [result, setResult] = useState<{
    fight: boolean
    precentegeoffight: string
    processing_time: string
  } | null>(null)
  const [error, setError] = useState<string | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0]
    if (selectedFile) {
      // Check if file is a video
      if (!selectedFile.type.startsWith("video/")) {
        setError("Please upload a video file")
        setFile(null)
        setVideoSrc(null)
        return
      }

      setFile(selectedFile)
      setError(null)
      setResult(null)

      // Create a URL for the video preview
      const url = URL.createObjectURL(selectedFile)
      setVideoSrc(url)
    }
  }

  const handleUploadClick = () => {
    fileInputRef.current?.click()
  }

  const handleSubmit = async () => {
    if (!file) {
      setError("Please upload a video first")
      return
    }

    setIsLoading(true)
    setError(null)

    try {
      // Create a FormData object to send the file
      const formData = new FormData()
      formData.append("file", file) // Note: Flask expects "file", not "video"

      // Replace with your Flask backend URL (when running locally)
      const response = await fetch("http://localhost:5000/api/predict", {
        method: "POST",
        body: formData,
        // No need to set Content-Type header, it will be set automatically for FormData
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || "Failed to process video")
      }

      const data = await response.json()
      setResult(data)
    } catch (err) {
      setError(err instanceof Error ? err.message : "An unknown error occurred")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 bg-gray-50">
      <Card className="w-full max-w-3xl">
        <CardHeader>
          <CardTitle className="text-2xl">Violence Detection System</CardTitle>
          <CardDescription>Upload a video to analyze for violent content</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* File Upload Area */}
          <div
            className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center cursor-pointer hover:bg-gray-50 transition-colors"
            onClick={handleUploadClick}
          >
            <input type="file" ref={fileInputRef} onChange={handleFileChange} accept="video/*" className="hidden" />
            <Upload className="mx-auto h-12 w-12 text-gray-400" />
            <p className="mt-2 text-sm text-gray-600">Click to upload or drag and drop</p>
            <p className="text-xs text-gray-500">MP4, AVI, MOV up to 100MB</p>
          </div>

          {/* Video Preview */}
          {videoSrc && (
            <div className="mt-4">
              <h3 className="text-sm font-medium mb-2">Video Preview:</h3>
              <video
                src={videoSrc}
                controls
                className="w-full rounded-md border border-gray-200"
                style={{ maxHeight: "300px" }}
              />
              <p className="text-sm text-gray-500 mt-1">
                {file?.name} ({(file?.size ? (file.size / (1024 * 1024)).toFixed(2) : "0.00")} MB)
              </p>
            </div>
          )}

          {/* Error Message */}
          {error && (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Loading State */}
          {isLoading && (
            <div className="space-y-2">
              <p className="text-sm font-medium">Processing video...</p>
              <Progress value={45} className="h-2" />
            </div>
          )}

          {/* Results */}
          {result && (
            <Alert className={result.fight ? "border-red-500 bg-red-50" : "border-green-500 bg-green-50"}>
              {result.fight ? (
                <XCircle className="h-4 w-4 text-red-600" />
              ) : (
                <CheckCircle className="h-4 w-4 text-green-600" />
              )}
              <AlertTitle className={result.fight ? "text-red-700" : "text-green-700"}>
                {result.fight ? "Violence Detected" : "No Violence Detected"}
              </AlertTitle>
              <AlertDescription>
                <div className="mt-2 text-sm">
                  <p>
                    <strong>Confidence:</strong> {result.precentegeoffight}%
                  </p>
                  <p>
                    <strong>Processing Time:</strong> {result.processing_time}ms
                  </p>
                </div>
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
        <CardFooter>
          <Button onClick={handleSubmit} disabled={!file || isLoading} className="w-full">
            {isLoading ? "Processing..." : "Analyze Video"}
          </Button>
        </CardFooter>
      </Card>
    </main>
  )
}

